import Link from 'next/link'

export default ({ team }) => (
  <div className="team-wrapper">
    {team.map(person => (
        <Link key={person.id} href={`/?q=${person.fields.name}`}>
          <a><img src={person.fields.avatar[0].url} alt={person.fields.name} /></a>
        </Link>
      )
    )}
  </div>
)
